.todo-list-item .todo-list-item-label {
	cursor: pointer;
	color:#fff;
}

.todo-list-item button{
	margin: 3px;
}

.todo-list-item.done .todo-list-item-label {
  text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label{
  color: #ffc107;
  font-weight: bold;
}